.header{background-color:#fff;box-shadow:0 2px 4px rgba(0,0,0,.1);padding:1rem 0}.container{max-width:1200px;margin:0 auto;padding:0 1rem;display:flex;justify-content:space-between;align-items:center}.logo{font-size:1.5rem;font-weight:bold;color:#1a1a1a;text-decoration:none}.nav{display:flex;gap:2rem}.link{color:#666;text-decoration:none;font-weight:500;transition:color .3s ease}.link:hover{color:#1a1a1a}